import { createGraphQLEffect } from "@servicenow/ui-effect-graphql";
import { glideRecordQuery } from "./query"
import { customActions } from "../constants"
import { handleMarkerAction, openInfoWindowHelper } from "./googleMapUtils";
import MarkerClusterer from '@google/markerclustererplus';
const url = require('../assets/markerClusterIcon.png');

let maxAutoZoomLevel = 12;

export const initializeMap =({ state, dispatch, updateState }) => {
	const { mapElementRef } = state;
	let mapSettings = state.properties.mapSettings;
	// if no mapSettings, do not load the map
	if(!mapSettings){
		return ;
	}
	maxAutoZoomLevel = mapSettings.maxAutoZoomLevel;
	let mapOptions = mapSettings.mapOptions;

	mapOptions["restriction"] = {
		latLngBounds: {north: 85, south: -85, west: -180, east: 180},
		strictBounds: true
	};
	let googleMap = new google.maps.Map(
		mapElementRef.current,
		mapOptions
	);
	handleMarkerAction(dispatch);
	if (googleMap, mapElementRef) {
		new async function() {
			updateState({
				googleMap: googleMap
			});
		}().then(dispatch(customActions.SET_MARKERS));
	} else
		console.log('Google map could not be initialized.');
}

export const getInfoWindowDetail = createGraphQLEffect(glideRecordQuery, {
	variableList: ["encodedQuery"],
	templateVarList: ['table', 'fields', 'sysId'],
	successActionType: customActions.MARKER_DATA_FETCH_SUCCEEDED,
});

export const setMarkersActionHandler = ({ state, updateState, dispatch }) => { 
	let { googleMap } = state;
	if (!googleMap) {
		console.error('Google Map is invalid before setting markers.');
	}
	if (state.markerCluster) state.markerCluster.setMap(null);
	state.markers.forEach((marker) => {
		marker.setMap(null);
	});
	const marker_data = state.properties.mapMarkers;
	if (marker_data.length == 0) {
		googleMap.setCenter({lat: 0, lng: 0});
		googleMap.setZoom(1);
		return;
	}
    let infowindow;
	let bounds = new google.maps.LatLngBounds();
	let markerMap = {};
	let markers = marker_data.map((data) => {
		const locationObj = {
			lat: data.lat,
			lng: data.lng,
		};

		const icon = {
			url: data.image,
			scaledSize: new google.maps.Size(50, 50)
		};

		const highlightedIcon = {
			url: data.highlightedIcon,
			scaledSize: new google.maps.Size(50, 50)
		};
		const marker = new google.maps.Marker({
			position: locationObj,
			map: googleMap,
			items: data.items,
			icon: icon,
			defaultIcon: icon,
			highlightedIcon: highlightedIcon
		});
		marker.items.forEach((item) => {
			markerMap[item["sys_id"]] = marker;
		});
        marker.addListener("click", function () {
            dispatch(customActions.MARKER_CLICKED, { marker : marker });
		});
		bounds.extend(marker.position);
		return marker;
	});
	googleMap.fitBounds(bounds);
	// check max zoom
	if (googleMap.getZoom() > maxAutoZoomLevel) {
		googleMap.setZoom(maxAutoZoomLevel);
	}
	
	googleMap.addListener('click', () => {
		dispatch(customActions.MAP_CLICKED);
        if (infowindow) infowindow.close();
    });
	let markerCluster = new MarkerClusterer(googleMap, markers,
        {styles: [{
		  url,
		  height: 50,
          width: 50,
		  anchorText:[20,2]}]
		});
	updateState({
		markers: markers,
		googleMap: googleMap,
		markerCluster: markerCluster,
		markerMap: markerMap
    });
}

export const mapClickedHandler = ({ state }) => {
	let { infowindow, selectedMarker } = state;
	if (infowindow) 
		infowindow.close();
	if (selectedMarker) 
		selectedMarker.setIcon(selectedMarker.defaultIcon);
}

export const markerClickedHandler = ({ action, state, updateState, dispatch }) => {
	let { infowindow, selectedMarker, googleMap } = state;
	let { marker } = action.payload
	if (infowindow) infowindow.close();
	if (selectedMarker) selectedMarker.setIcon(selectedMarker.defaultIcon);
	infowindow = new google.maps.InfoWindow();
	updateState({   
		infowindow : infowindow,
		selectedMarker : marker
	});
	openInfoWindowHelper(marker, googleMap, infowindow, dispatch);
}

export const markerDataFetchSucceededHandler = (coeffects) => {
	const {
		action: { payload, meta },
		state,
		dispatch,
		updateState,
		updateProperties,
	} = coeffects;
	const { googleMap, selectedMarker, infowindow}  = state;
	const table = (meta["options"]["templateVars"]["table"]||"");
	const sysId = (meta["options"]["templateVars"]["sysId"]||"");
	const result = (payload["data"]["GlideRecord_Query"][table]["_results"][0]||{});
	const keys = Object.keys(result);
	let content; 
	if (keys && keys.length > 0) {
		//the first field will be the info window title
		content = `<div class="info-window-content">
						<h3 class="info-window-header">${result[keys[0]].displayValue}</h3>
						<button class="open-record-button" onclick="openWorkspaceTab('${sysId}', '${table}')"></button>
						<dl>`;
		for (let i = 1; i < keys.length; i++) {
			let field = result[keys[i]];
			if (field['_reference']) {
				let fieldKeys = Object.keys(field['_reference']);
				for (let j = 0; j< fieldKeys.length; j++) {
					field = field['_reference'][fieldKeys[j]];
					content +=  `<dt>${field.label.charAt(0).toUpperCase() +field.label.slice(1)}</dt>`
					if (field.displayValue)
						content += `<dd>${field.displayValue}</dd>`;
				}
			} else {
				content +=  `<dt>${field.label.charAt(0).toUpperCase() + field.label.slice(1)}</dt>`
				if (field.displayValue)
					content += `<dd>${field.displayValue}</dd>`;
				}
		}
		content += `</dl> </div>`
	}
	infowindow.setContent(content);
	infowindow.open(googleMap, selectedMarker);
}

export const panToMarkerActionHandler = ({ state, updateState }) => {
	const recordId = state.properties.selectedRecordId;
	const { googleMap, markerMap, selectedMarker }  = state;
	let marker = markerMap[recordId];
	if (selectedMarker) selectedMarker.setIcon(selectedMarker.defaultIcon);
	if (marker) {
		marker.setIcon(marker.highlightedIcon);
		googleMap.panTo(marker.position);
        googleMap.setZoom(15);
		
	}
	updateState({
		selectedMarker : marker
	});
}