import { SN_FSM_CARD_ACTION_CLICKED, DECLARATIVE_ACTION, CARD_CLICK_ACTION, NOW_CARD_SELECTED_SET, NOW_CARD_HEADER_ACTION_CLICKED, SN_FSM_CARD_OVERFLOW_ACTION_CLICKED, SN_FSM_CARD_COMP_NAME } from '../constants';
import { prepareDAPayload } from '../utils';

const actions = {
    [SN_FSM_CARD_ACTION_CLICKED]: {
        schema: {
            type: 'object'
        }
    },
    [DECLARATIVE_ACTION]: {
        schema: {
            type: 'object'
        }
    },
    [NOW_CARD_SELECTED_SET]: {
        schema: {
            type: 'object'
        }
    },
    [CARD_CLICK_ACTION]: {
        schema: {
            type: 'object'
        }
    },
    [SN_FSM_CARD_OVERFLOW_ACTION_CLICKED]: {
        schema: {
            type: 'object'
        }
    }
};

const actionHandlers = {
    [SN_FSM_CARD_ACTION_CLICKED]: (coeffects) => {
        console.log(SN_FSM_CARD_ACTION_CLICKED + " action dispatched ");
        coeffects.dispatch(DECLARATIVE_ACTION, {action: prepareDAPayload(coeffects.properties.actionDispatch, coeffects.action.type, {sysId: coeffects.properties.sysId}, coeffects.action.payload)});
    },
    [NOW_CARD_SELECTED_SET]: (coeffects) => {
        console.log(NOW_CARD_SELECTED_SET + " action dispatched");
        coeffects.dispatch(DECLARATIVE_ACTION, {action: prepareDAPayload(coeffects.properties.actionDispatch, coeffects.action.type, {sysId: coeffects.properties.sysId, taskState: coeffects.properties.taskState}, coeffects.action.payload)});
    },
    [NOW_CARD_HEADER_ACTION_CLICKED]: (coeffects) => {
        if(coeffects.host.tagName.toUpperCase()===SN_FSM_CARD_COMP_NAME)
            coeffects.dispatch(SN_FSM_CARD_OVERFLOW_ACTION_CLICKED, { type: coeffects.action.type, context: { sysId: coeffects.properties.sysId }, payload: coeffects.action.payload });
    },
    [SN_FSM_CARD_OVERFLOW_ACTION_CLICKED]: (coeffects) => {
        console.log(SN_FSM_CARD_OVERFLOW_ACTION_CLICKED + " action dispatched ");
        coeffects.dispatch(DECLARATIVE_ACTION, {action: prepareDAPayload(coeffects.properties.actionDispatch, coeffects.action.type, {sysId: coeffects.properties.sysId}, coeffects.action.payload.payload)});
    },
};

export { actions, actionHandlers };
