import style from "./style.scss";
import { createCustomElement } from "@servicenow/ui-core";
import { SN_FSM_CARD_OVERFLOW_ACTION_CLICKED, NOW_CALENDAR_TEMPLATE_COMPONENT_EVENT } from '../constants';
import styles from "./style.scss";

createCustomElement("sn-fsm-agent-card-wrapper", {
	styles,
	properties: {
		section: {
			default: {
				sysId: "ff969201d7b3020058c92cf65e61036d",
				title: "Alex Ray",
				Group: "NorCal Technicians",
				userName: "alex.ray",
				cardSlotsData: {
					Groups: { value: "NorCal Technicians", showLabel: false },
					Location: {
						value: "2200 Powell Street, San Francisco,CA",
						showLabel: false,
					},
					"Distance to Task": { value: "NA", showLabel: true },
					"Available Parts": { value: "NA", showLabel: true },
					"Matching Skills": { value: "NA", showLabel: true },
					"Agent Status": { value: "NA", showLabel: true },
					"Schedule Status": { value: "Behind", showLabel: true },
                },
                headerActions: {
                    overflowActions: [
                        {
                            id: "dropdown1",
                            items: [
                                { id: "openAgentTab", label: "Open" },
                                { id: "viewOnMap", label: "View on Map" },
                            ],
                            icon: "ellipsis-v-outline",
                            label: "More actions",
                            variant: "primary",
                        },
                    ],
                    cardActions: [
                        {
                            id: "infopopover",
                            type: "popover",
                            icon: "eye-outline",
                            label: "Agent Details",
                        },
                    ],
                },
                popoverData: {
                    heading: "heading",
                    dir: "rtl",
                    popoverFieldData: [
                        { label: "First Name", value: "Firstname placeholder" },
                        { label: "Last Name", value: "Lastname placeholder" }
                    ],
                }
			},
		},
	},
	view(state, dispatch) {
		let { section } = state.properties;
		let titleLink = {
			text: section["title"],
			size: "md",
		};

		return (
			<div className="agent-card-wrapper">
				<sn-fsm-card
					titleLink={titleLink}
					type="agent"
					subheading=""
					headerActions={section.headerActions}
					sysId={section.sysId}
					cardSlotsData={section.cardSlotsData}
					popoverData={section.popoverData}
				></sn-fsm-card>
			</div>
		);
	},
	action: {
		[NOW_CALENDAR_TEMPLATE_COMPONENT_EVENT]: {
			schema: {
				type: "object",
			},
		},
	},
	actionHandlers: {
		[SN_FSM_CARD_OVERFLOW_ACTION_CLICKED]: (coeffects) => {
			console.log(NOW_CALENDAR_TEMPLATE_COMPONENT_EVENT + " action dispatched");
			coeffects.dispatch(NOW_CALENDAR_TEMPLATE_COMPONENT_EVENT, {
				data: {
					user_id: coeffects.action.payload.context.sysId,
					action_id: coeffects.action.payload.payload.action.id,
				},
			});
		},
	},
});
