import '@servicenow/now-dropdown';
import '@servicenow/now-rich-text';
import {t} from "sn-translate"

export default (state, { dispatch, updateState, updateProperties }) => {
	const { properties: { dropdownId, dropdownLabel, dropdownItems, selectedItems} } = state;
	return (
		<div
			className="labeledDropdown"
			dropdownId={dropdownId}
		>
			<div className="labelContainer"><now-rich-text html={dropdownLabel}></now-rich-text></div>
			<div className="dropdownContainer">
				<now-dropdown size="md" select="single" variant="secondary" configAria={{ "aria-label": t("Select {0}", dropdownLabel) }} selectedItems={selectedItems} items={dropdownItems} />
			</div>
		</div>
	)
}
