import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import styles from "./style.scss";
import view from "./view";
import {
	demoPopoverFieldData,
	demoHeaderActions,
	demoContextData,
} from "./demoData";
import { actions, actionHandlers } from "./actions";

/**
 * Component for displaying additional content for an object in a popover in the field service pages
 *
 * ```jsx
 *  <now-popover opened={false} >
 *      <now-button slot="trigger" label="Check out my popover" />
 *      <sn-fsm-popover-content slot="content"
 *          heading="Heading"
 *          subheading="Subheading"
 *          popoverFieldData={popoverFieldData}
 *          headerActions={headerActions}
 *          contextData={contextData}>
 *      </sn-fsm-popover-content>
 *  </now-popover>
 * ```
 *
 * @seismicElement sn-fsm-popover-content
 * @summary Component for displaying additional content for an object in a popover in the field service pages
 */
createCustomElement("sn-fsm-popover-content", {
	renderer: {
		type: snabbdom,
		view,
	},
	properties: {
		/**
		 * Card title
		 * @type {string}
		 */
		heading: {
			default: "",
			schema: {
				type: "string",
			},
		},
		/**
		 * Card subtitle
		 * @type {string}
		 */
		subheading: {
			default: "",
			schema: {
				type: "string",
			},
		},
		/**
		 * List of field value pairs that will be displayed in the card body
		 * @type {{{"label": "Short Description", "value": "Short Description 1", "label": "Number", "value": "WOT1234"}}
		 */
		popoverFieldData: {
			default: demoPopoverFieldData,
			schema: {
				type: "array",
				items: {
					type: "object",
					properties: {
						label: {
							type: "string",
						},
						value: {
							type: "string",
						},
					},
				},
			},
		},
		/**
		 * Card actions and overflow actions displayed in the card header 
		 * @type {{"overflowActions":[{"id":"dropdown1","items":[{"id":"dropdownItem1","label":"Open"},{"id":"dropdownItem2","label":"Auto Assign"},{"id":"dropdownItem3","label":"Show Siblings"}],"icon":"ellipsis-v-outline","label":"More actions","variant":"primary"}],"iconActions":[{"id":"siblings","type":"icon","icon":"documents-fill","label":"Has sibling tasks","className":"titleIcon"},{"id":"flagtask","type":"button-stateful","icon":"flag-outline","label":"Flag Task"}]}}
		 */
		headerActions: {
			schema: {
				type: "object",
				properties: {
					overflowActions: {
						type: "array",
						items: {
							type: "object",
							properties: {
								id: {
									type: "string",
								},
								items: {
									type: "array",
									items: {
										type: "object",
										properties: {
											id: {
												type: "string",
											},
											label: {
												type: "string",
											},
										},
									},
								},
								icon: {
									type: "string",
								},
								label: {
									type: "string",
								},
								variant: {
									type: "string",
								},
							},
						},
					},
					cardActions: {
						type: "array",
						items: {
							anyOf: [
								{
									type: "object",
									properties: {
										id: {
											type: "string",
										},
										type: {
											type: "string",
										},
										icon: {
											type: "string",
										},
										label: {
											type: "string",
										},
										selected: {
											type: "boolean",
										},
									},
								},
								{
									type: "object",
									properties: {
										id: {
											type: "string",
										},
										type: {
											type: "string",
										},
										icon: {
											type: "string",
										},
										label: {
											type: "string",
										},
									},
								},
							],
						},
					},
				},
			},
			default: demoHeaderActions,
		},
		/**
		 * Optional contextual data used in action payloads
		 * @type {{sys_id: "SYS_ID", record_type: "RECORD_TYPE"}}
		 */
		contextData: {
			schema: {
				type: "object",
				properties: {
					sys_id: {
						type: "string",
					},
					record_type: {
						type: "string",
					},
				},
			},
			default: demoContextData,
		},
		/**
		 * Optional width specification for the popover
		 * @type {string}
		 */
		width: {
			schema: {
				type: "string"
			}
		}
	},
	styles,
	actions,
	actionHandlers,
});
