import { createCustomElement, actionTypes } from "@servicenow/ui-core";
import snabbdom, { createRef } from '@seismic/snabbdom-renderer';
import { loadGoogleApi } from './googleLoadMap';
import {
	getInfoWindowDetail,
	initializeMap,
	panToMarkerActionHandler,
	setMarkersActionHandler,
	markerDataFetchSucceededHandler,
	markerClickedHandler,
	mapClickedHandler
} from './handlers';
import styles from "./styles.scss";
import view from './view';
import { customActions } from "../constants";
const { COMPONENT_BOOTSTRAPPED } = actionTypes;

/**
 * Component to display a Google map with markers to help to visually identify locations of people, tasks, and objects; data rendered on the map comes from the Map markers property
 *
 * ```jsx
 *  <sn-fsm-google-map mapSettings={mapSettings} mapMarkers={mapMarkers}></sn-fsm-google-map>
 * ```
 *
 * @seismicElement sn-fsm-google-map
 * @summary Component to display a Google map with markers to help to visually identify locations of people, tasks, and objects; data rendered on the map comes from the Map markers property
 */
createCustomElement("sn-fsm-google-map", {
	renderer: { type: snabbdom },
	view,
	styles,
	initialState: {
		mapElementRef: createRef(),
		infowindow: null,
		markers: [],
		markerMap: {},
		markerCluster: null,
		selectedMarker: null,
		googleMap: null,
	},

	properties: {
		/**
		 * Settings used for rendering the Google map
		 * @type {{"maxAutoZoomLevel":12,"mapOptions":{"styles":"","mapTypeId":"roadmap","streetViewControl":true,"restriction":{"latLngBounds":{"east":180,"north":85,"south":-85,"west":-185},"strictBounds":true}}}}
		 */
		mapSettings: {
			default: {},
			schema: {
				type: "object",
				properties: {
					maxAutoZoomLevel: {
						type: "integer"
					},
					mapOptions: {
						type: "object",
						properties: {
							styles: {
								type: "string"
							},
							mapTypeId: {
								type: "string"
							},
							streetViewControl: {
								type: "boolean"
							},
							restriction: {
								type: "object",
								properties: {
									latLngBounds: {
										type: "object",
										properties: {
											east: { type: "number" },
											west: { type: "number" },
											north: { type: "number" },
											south: { type: "number" }
										},
									},
									strictBounds: { type: "boolean" }
								}
							}
						}
					}
				}
			}
		},
		/**
		 * Key used for accessing Google maps and APIs
		 * @type {{client: "gme-servicenow", key: "", method: "key"}}
		 */
		googleKey: {
			default: {},
			onChange(currentValue, previousValue, dispatch) {
				dispatch(customActions.LOAD_MAP_API);
			},
			schema: {
				type: "object",
				properties: {
					client: { type: "string" },
					key: { type: "string" },
					method: { type: "string" }
				}
			}
		},
		/**
		 * List of objects that need to be displayed on the map
		 * @type {[{"items":[{"sysId":"ff969201d7b3020058c92cf65e61036d","table":"sys_user","displayValue":"Alex Ray","type":"Field Service Agent","fields":"name { label value displayValue } \t\t\t\tagent_status { label value displayValue } \t\t\t\ton_schedule { label value displayValue } \t\t\t\tmobile_phone { label value displayValue } \t\t\t\temail { label value displayValue }"}],"long":-122.411594,"lat":37.805157,"image":"marker-techs-colocated.png","highlightedIcon":"marker-techs-colocated-on-focus.png"}]}
		 */
		mapMarkers: {
			default: [],
			onChange(currentValue, previousValue, dispatch) {
				dispatch(customActions.SET_MARKERS);
			},
			schema: {
				type: "array",
				items: {
					type: "object",
					properties: {
						items: {
							type: "array",
							items: {
								type: "object",
								properties: {
									sysId: { type: "string" },
									table: { type: "string" },
									displayValue: { type: "string" },
									type: { type: "string" },
									fields: { type: "string" }
								}
							}
						},
						lng: { type: "number" },
						lat: { type: "number" },
						image: { type: "string" },
						highlightedIcon: { type: "string" }
					}
				}
			}
		},
		/**
		 * Record ID of marker to highlight and center on in the map
		 * @type {string}
		 */
		selectedRecordId: {
			default: "",
			onChange(currentValue, previousValue, dispatch) {
				dispatch(customActions.PAN_TO_MARKER);
			},
			schema: { type: "string" }
		}
	},

	actionHandlers: {
		//[actionTypes.COMPONENT_BOOTSTRAPPED]: loadGoogleApi,
		[customActions.LOAD_MAP_API]: loadGoogleApi,
		[customActions.INITIALIZE_MAP]: initializeMap,
		[customActions.SET_MARKERS]: setMarkersActionHandler,
		[customActions.MARKER_DATA_FETCH_SUCCEEDED]: markerDataFetchSucceededHandler,
		[customActions.MARKER_DATA_FETCH]: getInfoWindowDetail,
		[customActions.PAN_TO_MARKER]: panToMarkerActionHandler,
		[customActions.MARKER_CLICKED]: markerClickedHandler,
		[customActions.MAP_CLICKED]: mapClickedHandler
	},
});
