import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import styles from "./style.scss";
import view from "./view";
import { actions, actionHandlers } from "./actions";
import { demoPopoverData, demoCardSlotsData, demoTitleLink, demoHeaderActions } from './demoData';

/**
 * A container for specific information about field service entity in a simple, easily scannable format.
 *
 * ```jsx
 *  <sn-fsm-card
 *   titleLink={titleLink}
 *   titleIcon={titleIcon}
 *   headerActions={headerActions}
 *   subheading={subheading}
 *   footerTags={footerTags}
 *   sysId={sysId}
 *   taskState={state}
 *   cardSlotsData={cardSlotsData}
 *   popoverData={popoverData}
 *   selected={selected}
 *   draggingInfo={draggingInfo}>
 *  </sn-fsm-card>
 * ```
 *
 * @seismicElement sn-fsm-card
 * @summary A container for specific information about field service entity in a simple, easily scannable format.
 */
createCustomElement("sn-fsm-card", {
    renderer: {
        type: snabbdom,
        view,
    },
    styles,
    properties: {
        /**
         * Defines the card's title
         * @type {{ "text": "Title Link", "id": "title" }}
         */
        titleLink: {
            schema: {
                type: "object",
                properties: {
                    text: {
                        type: "string"
                    },
                    id: {
                        type: "string"
                    }
                },
            },
            default: demoTitleLink,
        },
        /**
         * Name of the icon in dash-case, including the `-fill` or `-outline` suffix to
         * indicate whether the icon is filled or outlined.
         * This icon will appear besides the title.
         * @type {string}
         */
        titleIcon: {
            schema: { type: "string" },
            default: "",
            required: false,

        },
        /**
         * Property to specify cardActions and overflowActions on the card header
         * @type {{"overflowActions":[{"id":"dropdown1","items":[{"id":"dropdownItem1","label":"Open"}],"icon":"ellipsis-v-outline","label":"More actions","variant":"primary"}],"cardActions":[{"id":"flagtask","type":"button-stateful","icon":"flag-outline","label":"Flag Task"}]}}
         */
        headerActions: {
            schema: {
                type: 'object',
                "properties": {
                    "overflowActions": {
                        "type": "array",
                        "items": {
                            "type": "object",
                            "properties": {
                                "id": {
                                    "type": "string"
                                },
                                "items": {
                                    "type": "array",
                                    "items": {
                                        "type": "object",
                                        "properties": {
                                            "id": {
                                                "type": "string"
                                            },
                                            "label": {
                                                "type": "string"
                                            }
                                        }
                                    }
                                },
                                "icon": {
                                    "type": "string"
                                },
                                "label": {
                                    "type": "string"
                                },
                                "variant": {
                                    "type": "string"
                                }
                            }
                        }
                    },
                    "cardActions": {
                        "type": "array",
                        "items": {
                            "anyOf": [
                                {
                                    "type": "object",
                                    "properties": {
                                        "id": {
                                            "type": "string"
                                        },
                                        "type": {
                                            "type": "string"
                                        },
                                        "icon": {
                                            "type": "string"
                                        },
                                        "label": {
                                            "type": "string"
                                        },
                                        "selected": {
                                            "type": "boolean"
                                        }
                                    }
                                },
                                {
                                    "type": "object",
                                    "properties": {
                                        "id": {
                                            "type": "string"
                                        },
                                        "type": {
                                            "type": "string"
                                        },
                                        "icon": {
                                            "type": "string"
                                        },
                                        "label": {
                                            "type": "string"
                                        }
                                    }
                                }
                            ]
                        }
                    }
                }
            },
            default: demoHeaderActions
        },
        /**
         * Property for the card subheading that appears below the cardTitle
         * @type {string}
         */
        subheading: {
            schema: { type: "string" },
            default: "Sub heading",
        },
        /**
         * Property to specify the highlighted items on the card footer
         * @type {[{"label":"","value":{"type":"sn-fsm-highlighted-value","label":"Work in progress","bgColor":"#48C891"}},{"label":"","value":{"type":"now-sla-timer","taskId":"81689eb4df113100dca6a5f59bf263ed"}}]}
         */
        footerTags: {
            schema: {
                type: "array",
                "items": {
                    "anyOf": [
                        {
                            "type": "object",
                            "properties": {
                                "label": {
                                    "type": "string"
                                },
                                "value": {
                                    "type": "object",
                                    "properties": {
                                        "type": {
                                            "type": "string"
                                        },
                                        "label": {
                                            "type": "string"
                                        },
                                        "bgColor": {
                                            "type": "string"
                                        }
                                    }
                                }
                            }
                        },
                        {
                            "type": "object",
                            "properties": {
                                "label": {
                                    "type": "string"
                                },
                                "value": {
                                    "type": "object",
                                    "properties": {
                                        "type": {
                                            "type": "string"
                                        },
                                        "taskId": {
                                            "type": "string"
                                        },
                                        "showIcon": {
                                            "type": "boolean"
                                        }
                                    }
                                }
                            }
                        }
                    ]
                }
            },
            default: [],
        },
        /**
         * Property for the sysId (unique identifier) of card data
         * @type {string}
         */
        sysId: {
            schema: { type: "string" },
            default: "ff969201d7b3020058c92cf6t6e8036d",
        },
        /**
         * Property to supply the taskState of the card record
         * @type {integer}
         */
        taskState: {
            schema: { type: "integer" },
        },
        /**
         * Property to specify the data to show in card slots present between subheading and card footer
         * @type {{address: "Demo Location Address", assignedTo: "Demo Assigned To Name",}}
         */
        cardSlotsData: {
            schema: {
                type: "object",
                properties: {
                    address: {
                        type: "string"
                    },
                    assignedTo: {
                        type: "string"
                    }
                }
            },
            default: demoCardSlotsData,
        },
        /**
         * Property to specify the details object to be given to sn-fsm-popover-content component to show in the popover
         * @type {{"popoverFieldData": [{ "label": "field", "value": "value" }]}}
         */
        popoverData: {
            schema: {
                type: "object",
                properties: {
                    popoverFieldData: {
                        type: "array",
                        items: {
                            type: "object",
                            properties: {
                                label: {
                                    type: "string"
                                },
                                value: {
                                    type: "string"
                                }
                            }
                        }
                    },
                    dir: {
                        type: "string"
                    }
                }
            },
            default: demoPopoverData,
        },
        /**
         * Property to display card as selected
         * @type {boolean}
         */
        selected: {
            schema: { type: "boolean" },
            default: false,
        },
        /**
         * Property to provide card dragging capability and data
         * @type {{isDraggable: true, duration: 40 * 60 * 1000}}
         */
        draggingInfo: {
            schema: {
                type: "object",
                properties: {
                    isDraggable: {
                        type: "boolean"
                    },
                    duration: {
                        type: "integer"
                    }
                }
            },
            default: {}
        },
        actionDispatch: {
            schema: { type: "string" },
            default: "FSM_CARD_ACTION",
        }
    },
    actions,
    actionHandlers
});
