import { SN_FSM_POPOVER_ACTION_CLICKED, SN_FSM_POPOVER_OVERFLOW_ACTION_CLICKED, DECLARATIVE_ACTION, NOW_CARD_HEADER_ACTION_CLICKED, SN_FSM_POPOVER_CONTENT_COMP_NAME } from '../constants';
import { prepareDAPayload } from '../utils';

const actions = {
    [SN_FSM_POPOVER_ACTION_CLICKED]: {
        schema: {
            type: 'object'
        }
    },
    [SN_FSM_POPOVER_OVERFLOW_ACTION_CLICKED]: {
        schema: {
            type: 'object'
        }
    },
    [DECLARATIVE_ACTION]: {
        schema: {
            type: 'object'
        }
    },
};

const actionHandlers = {
    [SN_FSM_POPOVER_ACTION_CLICKED]: (coeffects) => {
        console.log(SN_FSM_POPOVER_ACTION_CLICKED + " action dispatched ");
        coeffects.dispatch(DECLARATIVE_ACTION, {action: prepareDAPayload("FSM_POPOVER_ACTION", coeffects.action.type, coeffects.properties.contextData, coeffects.action.payload)});
    },
    [SN_FSM_POPOVER_OVERFLOW_ACTION_CLICKED]: (coeffects) => {
        console.log(SN_FSM_POPOVER_OVERFLOW_ACTION_CLICKED + " action dispatched ");
        coeffects.dispatch(DECLARATIVE_ACTION, {action: prepareDAPayload("FSM_POPOVER_ACTION", coeffects.action.type, coeffects.properties.contextData, coeffects.action.payload.payload)});
    },
    [NOW_CARD_HEADER_ACTION_CLICKED]: (coeffects) => {
        if (coeffects.host.tagName.toUpperCase() === SN_FSM_POPOVER_CONTENT_COMP_NAME)
            coeffects.dispatch(SN_FSM_POPOVER_OVERFLOW_ACTION_CLICKED, { type: coeffects.action.type, context: coeffects.properties.contextData, payload: coeffects.action.payload });
    },
};

export { actions, actionHandlers };
