import { Fragment } from '@servicenow/ui-renderer-snabbdom'
import '@servicenow/now-card';
import '@servicenow/now-label-value';
import '@servicenow/now-heading';
import '@servicenow/now-icon';
import '@servicenow/now-button';

import { SN_FSM_POPOVER_ACTION_CLICKED } from '../constants';

export default (state, { dispatch, updateState, updateProperties }) => {
    const { properties: { popoverFieldData, heading, subheading, headerActions, contextData, width } } = state;
    let contentItems = popoverFieldData.filter((item) => { return item.label != 'shortDescription' && item.label != 'number' });
    contentItems = contentItems.map(({ label, value }) => {
        return { "label": label, "value": { "type": "string", "value": value } };
    });

    let iconActionElemsMarkup;
    if (headerActions && headerActions.iconActions)
        iconActionElemsMarkup = headerActions.iconActions.map(({ id, type, icon, label, selected, className }) => {
            switch (type) {
                case "icon":
                    return (<now-icon id={id} className={className} icon={icon} size="sm" configAria={{ "aria-label": label }} ></now-icon>)
                    break;
                case "button-stateful":
                    return (<now-button-stateful id={id} icon={icon} tooltipContent={label} configRole="button" size="sm" variant="tertiary" configAria={{ "aria-label": label }} selected={selected} bare={true} on-click={(e) => dispatch(SN_FSM_POPOVER_ACTION_CLICKED, { id: id, selected: !e.target.selected })} ></now-button-stateful>)
                    break;
            }
        });

    let subheadingMarkup;
    if (subheading && subheading.length > 0)
        subheadingMarkup = <now-label-value-inline className="subheading" label={subheading} value=""></now-label-value-inline>

    return (
        <div className="popoverContentContainer" tab-index="0" slot="content" aria-live="polite">
            <div className="heading">
                <now-card-header
                    heading={{ label: heading }}
                    dropdowns={headerActions.overflowActions}
                >
                    <div className="iconActionElems" style={{ "display": "flex" }} slot="metadata">
                        {iconActionElemsMarkup}
                    </div>
                </now-card-header>
                {subheadingMarkup}
            </div>
            <div className="contentSlot" style={{"width": width ? width : "auto"}}>
                <now-label-value-tabbed className="contentSlotItems"
                    items={contentItems} delimiter="," size="sm"
                ></now-label-value-tabbed>
            </div>
        </div>
    );
}