import { NOW_TOGGLE_CHECKED_SET, DECLARATIVE_ACTION } from '../constants';
import { prepareDAPayload } from '../utils';

const actions = {
    [NOW_TOGGLE_CHECKED_SET]: {
        schema: {
            type: 'object'
        }
    },
    [DECLARATIVE_ACTION]: {
        schema: {
            type: 'object'
        }
    }
};

const actionHandlers = {
    [NOW_TOGGLE_CHECKED_SET]: (coeffects) => {
        coeffects.dispatch(DECLARATIVE_ACTION, {action: prepareDAPayload(coeffects.properties.actionDispatch, coeffects.action.type, { toggleId: coeffects.properties.toggleId }, coeffects.action.payload.value)});
    },
};

export { actions, actionHandlers };