import '@servicenow/now-toggle';
import '@servicenow/now-rich-text';

export default (state, { dispatch, updateState, updateProperties }) => {
    const { properties: { toggleId, toggleLabel, defaultToggleSelection, disabled} } = state;
    return (
        <div
            className="labeledToggle"
            toggleId={toggleId}
        >
            <div className="labelContainer"><now-rich-text html={toggleLabel}></now-rich-text></div>
            <div className="toggleContainer"><now-toggle size="md" configAria={{ "aria-label": toggleLabel }} checked={defaultToggleSelection} disabled={disabled}></now-toggle></div>
        </div>
    )
}
