import {customActions} from "../constants";

export const loadGoogleApi = ({ state, dispatch, updateState }) => {
	const method = state.properties.googleKey["method"];
	if(!method){
		return ;
	}
	const key = method == "key" ? state.properties.googleKey["key"] : state.properties.googleKey["client"];
	const script = document.createElement("script");
	script.src = `https://maps.googleapis.com/maps/api/js?${method}=${key}&callback=initMap`;
	script.async = true;
	document.body.appendChild(script);
	window.initMap = initMap;
	function initMap() {
		dispatch(customActions.INITIALIZE_MAP);
	}
};