import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './style.scss';
import view from './view';
import { actions, actionHandlers } from './actions';

createCustomElement('sn-fsm-labeled-toggle', {
    renderer: {
        type: snabbdom,
        view
    },
    styles,
    properties: {
        toggleId: {
            schema: { type: 'string' },
        },
        toggleLabel: {
            schema: { type: 'string' },
        },
        defaultToggleSelection: {
            schema: { type: 'boolean' },
        },
        disabled: {
            schema: {type: 'boolean' },
        },
        actionDispatch: {
            schema: { type: "string" },
            default: "FSM_LABELED_TOGGLE_ACTION",
        }
    },
    actions,
    actionHandlers
});
