import { createCustomElement } from "@servicenow/ui-core";
import styles from "./eventBody.scss";
import '@servicenow/now-label-value';
import '@servicenow/now-rich-text';
createCustomElement("sn-fsm-event-body", {
  properties: {
    event: {
      default: {},
    },
    viewConfig: {
      default: {},
    },
    // UTC Timestamp
    viewStartMS: {
      default: 0,
    },
    // UTC Timestamp
    viewEndMS: {
      default: 0,
    },
    // you will find all calendar props here
  },
  styles,
  view(state, dispatch) {
    /**
     * @type {string}
     */
	const title = state.properties.event.title || "";
  const description = state.properties.event.description || "";
	const start = Math.max(state.properties.event.start,state.properties.viewStartMS);
  const mid = Math.min(Math.max(state.properties.event.mid, state.properties.viewStartMS),state.properties.viewEndMS);
	const end = Math.min(state.properties.event.end, state.properties.viewEndMS);
	const travelWidth = (mid - start)/(end - start)*100;

    const travelTimeStyle = {
	  backgroundColor: state.properties.event.bgColor,
	  width: travelWidth + "%",
	  filter: "brightness(60%)"
    };
    const eventMainStyle = {
		backgroundColor: state.properties.event.bgColor,
		width: (100-travelWidth)+"%"
	};
    const valueMarkup = <now-rich-text html={"<strong>" + title + "</strong>"}></now-rich-text>
    return (
      <div className="event-body">
        <div className="travel-time" style={travelTimeStyle}></div>
        <div className="event-main" style={eventMainStyle}>
			<div className="event-main-row" style={{fontWeight:"normal"}}>
				<now-label-value-stacked
					items={[
						{ "label": "", "value": { "type": "string", "value": description } },
					]}
					itemMinWidth="50" size="sm" align="vertical" truncated
				></now-label-value-stacked>
			</div>
			<div className="event-main-row">
				<now-label-value-stacked style={{fontWeight:"bold"}}
					items={[
						{ "label": "", "value": { "type": "jsx", "value": valueMarkup } },
					]}
					itemMinWidth="50" size="sm" align="vertical" truncated
				></now-label-value-stacked>
			</div>
        </div>
      </div>
    );
  },
});
