import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './style.scss';
import view from './view';
import { actions, actionHandlers } from './actions';

createCustomElement('sn-fsm-labeled-dropdown', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		dropdownId: {
			schema: { type: 'string' },
		},
		dropdownLabel: {
			schema: { type: 'string' },
			default: "Distance Unit"
		},
		dropdownItems: {
			schema: { type: "array" },
			default: [{"id":"mile","label":"Miles"},{"id":"km","label":"Kilometers"}]
		},
		selectedItems: {
			schema: { type: "array" },
			default: ["mile"]
		},
		actionDispatch: {
            schema: { type: "string" },
            default: "FSM_LABELED_DROPDOWN_ACTION",
        }
	},
	actions,
	actionHandlers
});
