import { createCustomElement } from "@servicenow/ui-core";

const rendermarkspans = function (state, dispatch) {

	 //get markspans from timeline section data
	const markspans = state.properties.section.markspans;	
	const viewStartMs = state.properties.viewStartMS;
	const viewEndMs = state.properties.viewEndMS;
	const calendarSpan = viewEndMs - viewStartMs;

	const parentStyle = {
		position: "relative",
		width: '100%',
		height: '100%'
	};

	return <div style={parentStyle}>
		{markspans.map( (item) => {
			const start = (item.startMs - viewStartMs) *100/ calendarSpan
			const end = (item.endMs - viewStartMs)*100/calendarSpan;
			const childStyle = {
				display: 'flex',
				height: '100%',
				position: 'absolute',
				width: `${end - start}%`,
				left: `${start}%`,
				backgroundColor: item.color,
				verticalAlign: 'middle',
				textAlign: "center",
				alignItems: "center",
				...item.style
			}
			return <div style={childStyle}>
				<span style={{margin: "auto"}}>{item.content? item.content: ''}</span>
			</div>
		})}
	</div>
}


createCustomElement("sn-fsm-markspan", {
	properties: {
		//more calendar prop can be added
		//timeline section data
		section: {
			default: {}
		},
		// UTC Timestamp
		viewStartMS: {
			default: 0
		},
		// UTC Timestamp
		viewEndMS: {
			default: 0
		}
	},
	//styles,
	view(state, dispatch) {		
		const section = state.properties.section;
		if(Array.isArray(section.markspans) && section.markspans.length > 0){
			return rendermarkspans(state, dispatch);	
		}	
	}
}) 
