import {  NOW_DROPDOWN_SELECTED_ITEMS_SET, DECLARATIVE_ACTION } from '../constants';
import { prepareDAPayload } from '../utils';

const actions = {
	[NOW_DROPDOWN_SELECTED_ITEMS_SET]: {
		schema: {
			type: 'object'
		}
	}
};

const actionHandlers = {
	[NOW_DROPDOWN_SELECTED_ITEMS_SET]: (coeffects) => {
		coeffects.dispatch(DECLARATIVE_ACTION, {action: prepareDAPayload(coeffects.properties.actionDispatch, coeffects.action.type, { dropdownId: coeffects.properties.dropdownId }, coeffects.action.payload.value)});
	},
};

export { actions, actionHandlers };
