import { Fragment } from "@servicenow/ui-renderer-snabbdom";
import '@servicenow/now-card';
import '@servicenow/now-button';
import '@servicenow/now-label-value';
import '@servicenow/now-heading';
import '@servicenow/now-icon';
import '@servicenow/now-popover';
import '@servicenow/now-sla-timer';
import '@servicenow/now-sla-timer/src/now-sla-timer-renderer';

import '../sn-fsm-popover-content';
import '../sn-fsm-highlighted-value';
import { prepareDAPayload } from '../utils';
import { DECLARATIVE_ACTION } from '../constants';

import cardDraggingImage from '../assets/card-dragging-state.png';

import { SN_FSM_CARD_ACTION_CLICKED } from '../constants';

export default (state, { dispatch, updateState, updateProperties }) => {
    const { properties: { titleLink, titleIcon, headerActions, subheading, footerTags, sysId, taskState, cardSlotsData, popoverData, selected, draggingInfo, actionDispatch } } = state;

    let subheadingMarkup;
    if(subheading && subheading.length>0)
        subheadingMarkup = <Fragment><div className="subheading"><now-heading label={subheading} level="4" variant="title-tertiary" hasNoMargin={true} wontWrap={true} ></now-heading></div></Fragment>

    let cardSlotsMarkUp;
    if (cardSlotsData)
        cardSlotsMarkUp = Object.entries(cardSlotsData).map(([attr, valueObj]) => {
            if (valueObj.showLabel) {
                return (<now-label-value-tabbed className="labeled"
                    items={[
                        { "label": attr, "value": { "type": "string", "value": valueObj.value } },
                    ]}
                    itemMinWidth="50" delimiter="," size="sm" align="vertical" truncated
                ></now-label-value-tabbed>)
            }
            return (<now-label-value-stacked className="nonLabeled"
                items={[
                    { "label": "", "value": { "type": "string", "value": valueObj.value } },
                ]}
                itemMinWidth="50" delimiter="," size="sm" align="vertical" truncated
            ></now-label-value-stacked>)
        });
    let footerTagsItems = footerTags.map((arrItem, i) => {
        if (arrItem.value.type == 'sn-fsm-highlighted-value') {
            let fsmHighlightedValueMarkup = <sn-fsm-highlighted-value value={arrItem.value.label} bgColor={arrItem.value.bgColor} />;
            arrItem.value.type = "jsx";
            arrItem.value.value = fsmHighlightedValueMarkup;
        } else if (arrItem.value.type == 'now-sla-timer-renderer') {
            let slaTimerMarkup = <now-sla-timer-renderer style={{ position: "relative", top: "-3px" }} show-icon={arrItem.value.showIcon} data={JSON.stringify(arrItem.value.slaObj)}></now-sla-timer-renderer>;
            arrItem.value.type = "jsx";
            arrItem.value.value = slaTimerMarkup;
        } else if (arrItem.value.type == 'now-sla-timer') {
            let slaTimerMarkup = <now-sla-timer style={{ position: "relative", top: "-3px" }} show-icon={arrItem.value.showIcon} task={arrItem.value.taskId} configuration={arrItem.value.configId ? arrItem.value.configId : ""}></now-sla-timer>;
            arrItem.value.type = "jsx";
            arrItem.value.value = slaTimerMarkup;
        }
        return (arrItem)
    });

    let img = document.createElement("img");
    img.src = cardDraggingImage;

    let popoverDir = popoverData.dir == "ltr" ? [{ target: 'center-end', content: 'center-start' }]
        : [{ target: 'center-start', content: 'center-end' }];

    let cardFooterMarkup;
    if (footerTagsItems && footerTagsItems.length > 0) {
        cardFooterMarkup = <Fragment>
            <div className="cardFooter">
                <now-card-footer split="unequal">
                    <div className="cardFooterItems" >
                        <now-label-value-stacked
                            items={footerTagsItems}
                            itemMinWidth="50" delimiter="," size="sm" align="horizontal"
                        ></now-label-value-stacked>
                    </div>
                </now-card-footer>
            </div>
        </Fragment>
    }

    return (
        <div style={{ "height": "100%" }}>
            <div
                className="cardContainer"
                taskId={"_" + sysId}
                id={"_" + sysId}
                draggable={draggingInfo && draggingInfo.isDraggable ? draggingInfo.isDraggable + "" : false + ""}
                on-dragstart={(e) => {
                    e.dataTransfer.setData("text/plain", e.target.taskId);
                    e.dataTransfer.setDragImage(img, 0, 0);
                    let left = 0;
                    dispatch(DECLARATIVE_ACTION, {action: prepareDAPayload(actionDispatch, 'dragging', { sysId: sysId, title: titleLink.text }, { id: sysId, ...draggingInfo, startPositionDifference: left, originatedFromInternal: false })});
                }}
            >
                <now-card size="md"
                    interaction="select"
                    manageSelected="true"
                    selected={selected}
                    configAria={{ "button": { 'aria-label': titleLink.text + ' card select' } }}
                    style={{"height": "100%"}}>
                    <div className="cardHeader">
                        <div className="cardTitleSection">
                            {titleLink && titleLink.text ? (<now-button-bare id={titleLink.id} label={titleLink.text} iconStart="" iconEnd="" highContrast={true} variant="secondary" size={titleLink.size ? titleLink.size : "sm"} hidePadding={false} configAria={{}} on-click={(e) => dispatch(SN_FSM_CARD_ACTION_CLICKED, { id: e.target.id })} ></now-button-bare>) : ''}
                            {titleIcon ? (<now-icon icon={titleIcon} className="titleIcon" size="sm" ></now-icon>) : ''}
                        </div>
                        <now-card-header
                            heading={{ label: '' }}
                            dropdowns={headerActions.overflowActions}
                        >
                            <div className="cardActionElems" style={{ "display": "flex" }} slot="metadata">
                                {headerActions.cardActions.map(({ id, type, icon, label, selected, className }) => {
                                    switch (type) {
                                        case "icon":
                                            return (<now-icon className="cardAction" id={id + '_' + sysId} className={className} icon={icon} size="sm" variant="tertiary" configAria={{ "aria-label": label }} ></now-icon>)
                                            break;
                                        case "button-stateful":
                                            return (<now-button-stateful className="cardAction" id={id + '_' + sysId} icon={icon} tooltipContent={label} configRole="button" size="sm" variant="tertiary" configAria={{ "aria-label": label }} selected={selected} manageSelected={true} bare={true} on-click={(e) => dispatch(SN_FSM_CARD_ACTION_CLICKED, { id: id, selected: !e.target.selected })}  ></now-button-stateful>)
                                            break;
                                        case "popover":
                                            return (<now-popover
                                                id={id + "_" + sysId}
                                                interaction-type="dialog"
                                                hide-tail={false}
                                                positions={popoverDir}
                                            >
                                                <now-button-iconic className="cardAction" slot="trigger" icon={icon} tooltipContent={label} size="sm" variant="tertiary" bare={true} configAria={{ "aria-label": label }}></now-button-iconic>
                                                <sn-fsm-popover-content
                                                    slot="content"
                                                    aria-live="polite"
                                                    heading={popoverData.heading && popoverData.heading.length>0 ? popoverData.heading : ''}
                                                    subheading={popoverData.subheading && popoverData.subheading.length>0 ? popoverData.subheading : ''}
                                                    popoverFieldData={popoverData.popoverFieldData}
                                                    headerActions={popoverData.headerActions ? popoverData.headerActions : []}
                                                    width={popoverData.width}
                                                />
                                            </now-popover>)
                                            break;
                                    }
                                })}
                            </div>
                        </now-card-header>
                    </div>
                    <div className="cardContent">
                        {subheadingMarkup}
                        {cardSlotsMarkUp}
                    </div>
                    {cardFooterMarkup}
                </now-card>
            </div>
        </div>
    )
}
